@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);

*{
margin: 0px;
padding: 0px;
}
body{
  color: white;
  background: url(https://images.unsplash.com/photo-1507842217343-583bb7270b66?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8bGlicmFyeXxlbnwwfHwwfHw%3D&w=1000&q=80);
  font-family: Poppins;
}
.main{
  width: 100%;
  margin: auto;
  display: block;
}

.center{
  position: relative;
  top: 80px;
  width: 25%;
  margin: auto;
  display: block;
  background-color:  rgb(20 33 35 / 83%);
  height: auto;
  padding-bottom: 100px;
  border-radius: 20px;
}

.center h1{
  position: relative;
  width: 70%;
  margin: auto;
  top: 20px;
  margin-bottom: 20px;
  text-align: center;
}


.input{
  width: 90%;
  margin: auto;
}

.input input{
  margin: 10px;
  height: 35px;
  border: none;
  width: 220px;
  background-color: rgba(240, 248, 255, 0);
  border-bottom: 2px solid rgb(175, 173, 173);
  font-size: 25px;
  margin-left: 35px;
  color: white;
}

.input button{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 30px;
  background-color: rgb(182 200 147);
  border: none;
}


.input button:hover{
  background-color:#54d38e;
}

.center ol{
  display: block;
  margin: auto;
  width: 70%;
  
  align-self: center;
  align-items: center;
}

.center ol li{
  font-size: 20px;
 
  list-style: none;
  margin-left: 15px;
}

.list{
  display: flex;
  padding-bottom: 8px;
}


.list span button {
  width: 25px;
height: 25px;
border-radius: 50%;
background-color: rgb(182 200 147);
border: none;
}
.list span button:hover {
  background-color:#d35454; 
}

@media screen and (max-width: 1300px) {

  .main{
    width: 100%;
  }

  .center{
    width: 35%;
  }

  .input{
    justify-self: center;
  }


}
@media screen and (max-width: 1100px) {

  .main{
    width: 100%;
  }

  .center{
    width: 45%;
  }

}
@media screen and (max-width: 850px) {

  .main{
    width: 100%;
  }

  .center{
    width: 50%;
  }



}
@media screen and (max-width: 750px) {

  .main{
    width: 100%;
  }

  .center{
    width: 60%;
  }

}
@media screen and (max-width: 650px) {

  .main{
    width: 100%;
  }

  .center{
    width: 70%;
  }


  .center h1{
    text-align: center;
  }

}
@media screen and (max-width: 650px) {

  .main{
    width: 100%;
  }

  .center{
    width: 90%;
  }


  .center h1{
    text-align: center;
    width: 100%;
  }

  .input{
    justify-self: center;
    font-size: 20px;
  }












  .input input{
    margin: 10px;
    height: 35px;
    border: none;
    width: 200px;
    border-bottom: 2px solid rgb(175, 173, 173);
    color: white;
  }
  
  .input button{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    font-size: 30px;
  }
}
